body {
  margin: 0;
  background: white;
  color: black;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.movies {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
}
.movie {
  margin-top: 30px;
  margin-left: 20px;
  width: 600px;
  height: 600px;
}
ul {
  display: flex;
  list-style: none;
}
li {
  margin-right: 10px;
}
